.admin-menu-big-buttons {
  overflow: hidden;
}
.admin-menu-big-buttons a {
  text-decoration: none;
  color: #000;
}
.admin-menu-big-buttons a .big-button {
  width: 23.75%;
  height: 200px;
  background-color: #d6eef9;
  float: left;
  position: relative;
  margin-left: 1.66%;
}
.admin-menu-big-buttons a .big-button.first {
  margin-left: 0;
}
.admin-menu-big-buttons a .big-button span {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  font-family: "Nunito", sans-serif;
  font-size: 30px;
  text-align: center;
}

.latest-animals {
  clear: left;
  margin-top: 2em;
}